/* General Styling */
body {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f9f9f9;
    color: #333;
    line-height: 1.6;
    overflow-x: hidden;
}

/* Navbar Styling */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(45deg, #5196c7, #ffe869);
    padding: 20px 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    position: sticky;
    top: 0;
    z-index: 1000;
    transition: background 0.5s ease, box-shadow 0.5s ease;
}

.navbar:hover {
    background: linear-gradient(45deg, #ffe869, #5196c7);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

/* Logo and Branding */
.logo-container {
    display: flex;
    align-items: center;
}

.logo {
    height: 40px; /* Logo size */
    width: 40px;
    margin-right: 10px;
}

.text-container {
    display: flex;
    flex-direction: column; /* Stack h1 and p vertically */
    align-items: flex-start;
    white-space: nowrap; /* Prevent text from wrapping */
}

.text-container h1 {
    font-size: 1.3em; /* Slightly increased h1 size */
    font-weight: bold;
    margin: 0;
    color: white;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.text-container p {
    font-size: 0.8em;
    margin: 0;
    color: white;
    opacity: 0.8;
}

/* Hamburger Menu */
.hamburger {
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: none;
    border: none;
    cursor: pointer;
    z-index: 1100;
    outline: none;
}

.hamburger .bar {
    width: 20px;
    height: 2px;
    background: white;
    margin: 3px 0;
    transition: all 0.3s ease-in-out;
}

.hamburger.open .bar:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
}

.hamburger.open .bar:nth-child(2) {
    opacity: 0;
}

.hamburger.open .bar:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -5px);
}

/* Navigation Links */
.nav-links {
    display: flex;  /* Ensure the items are in a single line */
    align-items: center;
    justify-content: space-between; /* Spread the links across the navbar */
    transition: all 0.5s ease;
}

.nav-links ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;  /* Ensure links are in a row */
    align-items: center;
    white-space: nowrap; /* Prevent links from wrapping to the next line */
}

.nav-links ul li {
    margin-left: 12px; /* Increased space between links */
}

.nav-links ul li a {
    text-decoration: none;
    color: white;
    font-size: 0.85em; /* Decreased font size for links */
    padding: 8px 15px;
    transition: background 0.3s ease, color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
    border-radius: 5px;
}

.nav-links ul li a:hover {
    background-color: rgb(3, 110, 198);
    color: #f3ff10;
    transform: scale(1.1);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

/* Mobile Styling */
@media (max-width: 768px) {
    .hamburger {
        display: flex;
    }

    .nav-links {
        position: fixed;
        top: 0;
        right: -100%;
        height: 100%;
        width: 230px;
        flex-direction: column;
        background: linear-gradient(45deg, #5196c7, #ffe869);
        justify-content: center;
        align-items: center;
        transition: right 0.3s ease-in-out;
        z-index: 1000;
    }

    .nav-links.active {
        right: 0;
    }

    .nav-links ul {
        flex-direction: column;
        width: 100%;
        padding: 0;
    }

    .nav-links ul li {
        margin: 12px 0;
    }

    .nav-links ul li a {
        width: 100%;
        text-align: center;
        font-size: 1.2em;
    }
}

/* Tablet Devices */
@media (max-width: 1024px) {
    .logo {
        height: 30px;
        width: 30px;
    }

    .text-container h1 {
        font-size: 1.1em; /* Slightly larger size for tablet */
    }

    .nav-links ul li a {
        font-size: 0.85em;
    }

    .nav-links ul li {
        margin-left: 8px;
    }
}

/* Large Screens */
@media (min-width: 1600px) {
    .logo {
        height: 50px;
        width: 50px;
    }

    .text-container h1 {
        font-size: 1.6em; /* Slightly larger font size for h1 */
    }

    .nav-links ul li a {
        font-size: 1.1em;
        padding: 12px 18px;
    }

    .nav-links ul li {
        margin-left: 15px;
    }
}
